body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

#container{
  margin: 0 auto;
  width: 976px;
  -ms-flex-align: center;
  align-items: center;
}
.header{
  height: 96px;
  width: 976px;
  background-color: #AAB7B8;
}
.header img{
  height: 32px;
  width: 32px;
  padding: 32px 0 32px 32px;
  color: #AAB7B8  ;
}
.header h2{
  margin-left: 85px;
  margin-top: -66px;
  color: white;
}
.content{
  margin-top: -40px;
  background-color: #f5f5f0;

}
.content h2 {
  padding-top: 80px;
  margin-left: 32px;
  color: #757575;
  margin-top: 40px;
}
 table{
   margin-left: 32px;
   background-color: #fff;
   width: 912px;
 }
 .table-body{
   padding-bottom: 100px;
 }
 table th{
   color: #757575;
   font-size: 14px;
   font-weight: 500;
   text-align: left;
   line-height: 16px;
   padding-left: 26px;
   height: 48px;
   border-bottom: 1px solid #f5f5f0;
   width: 200px;
   cursor: pointer;
 }
 table .email{
    width: 350px;
 }

 table tr{
   height: 72px;
 }
 table td{
  text-align: left;
  padding-left: 24px;
  color: #505050;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  border-bottom: 1px solid #f5f5f0;
  width: 200px;
 }

.fa-pencil-alt{
  width: 24px;
  height: 24px;
  font-size: 24px;
  color: #909090;
  margin-left: 60px;
  cursor: pointer;
 }

.fa-trash{
  width: 24px;
  height: 24px;
  font-size: 24px;
  color: #909090;
  float: right;
  padding-right: 30px;
  cursor: pointer;

}
form{
  width: 912px;
  margin-left: 32px;
  background-color: #fff;
  padding: 16px 0 16px 0;
  margin-bottom: 10px;
}

input{
  height: 40px;
  background-color: #f5f5f0;
  border: 1px solid #E5E7E9;
  margin-left: 16px;
}


input[type='text']{
  padding-left: 16px;
}
input[type='tel']{
  padding-left: 16px;
  width: 175px;
}
input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
input[type='email']{
  padding-left: 16px;
  width: 270px;
}
form button{
  float: right;
  margin-right: 16px;
  padding: 0 20px 0 20px;
  background-color: #f5f5f0;
  border: 1px solid #F2F2F2;
  height: 44px;
}
.add{
  background-color: #07f;
  color: #fff;
  font-size: 16px;
  font-weight: 500;
}
button {
  color: #909090;
  font-weight: bold;
  cursor: pointer;
  margin-left: -10px;
}
.save{
  background-color: #07f;
  color: #fff;
  font-size: 14px;
}
.cancel{
  color: #07f;
  font-size: 14px;
}

/*FOR VALIDATION*/
.invalid {
  -webkit-box-shadow: 0 0 5px 1px red;
          box-shadow: 0 0 5px 1px red;
}

.error {
  color: red;
  font-size: 12px;
  display: inline-block;
  padding-right: 215px ;
}

/*for sorting*/
table th.asc:after{
  content: ' \2193';
  color:green;
}
table th.desc:after{
  content: ' \2191';
  color:green;
}
